
  import { defineComponent } from 'vue';
  import Icon from '@/components/icon/Icon.vue';
  import { MessageType, useMessenger } from '@/components/messenger/use/messenger';
  import { IMessage } from '@/components/messenger/Messenger';

  export default defineComponent({
    name: 'Messenger',
    components: { Icon },
    setup() {
      const { messages, deleteMessage } = useMessenger();

      const getColor = ({ type }: IMessage) => {
        switch (type) {
          case MessageType.Error:
            return 'red';
          case MessageType.Warning:
            return 'yellow';
          case MessageType.Info:
            return 'blue';
          case MessageType.Success:
            return 'green';
          default:
            return 'blue';
        }
      };

      const getIconProps = ({ type }: IMessage) => {
        switch (type) {
          case MessageType.Error:
            return {
              name: 'ic24-warning',
              color: '--red-500',
            };
          case MessageType.Warning:
            return {
              name: 'ic24-warning',
              color: '--yellow-500',
            };
          case MessageType.Info:
            return {
              name: 'ic24-info',
              color: '--blue-500',
            };
          case MessageType.Success:
            return {
              name: 'ic24-success',
              color: '--green-500',
            };
        }
      };

      const pauseHold = () => {
        messages.value.forEach(({ timer }) => {
          if (timer) {
            timer.pause();
          }
        });
      };

      const resumeHold = () => {
        messages.value.forEach(({ timer }) => {
          if (timer) {
            timer.resume();
          }
        });
      };

      return { messages, getColor, getIconProps, deleteMessage, pauseHold, resumeHold };
    },
  });
